"use client";

import { useState, useEffect } from "react";
import { useRouter } from "next/navigation";
import { zodResolver } from "@hookform/resolvers/zod";
import { useForm } from "react-hook-form";
import Link from "next/link";
import { motion } from "framer-motion";

import { useToast } from "@/shared/ui/use-toast";
import { loginSchema, LoginFormValues } from "../model";
import { useAuthContext } from "@/app/providers/auth-provider";
import { SpaceInput } from "@/shared/ui/space-input";
import { SpaceButton } from "@/shared/ui/space-button";
import { SpaceProgressLoader } from "@/shared/ui/space-progress-loader";
import { FlashTransition } from "@/shared/ui/flash-transition";

export function LoginForm() {
  const router = useRouter();
  const { toast } = useToast();
  const { login, isLoading } = useAuthContext();
  const [serverError, setServerError] = useState<string | null>(null);
  const [isSubmitting, setIsSubmitting] = useState(false);
  const [authProgress, setAuthProgress] = useState(0);
  const [showPortal, setShowPortal] = useState(false);
  const [loginSuccess, setLoginSuccess] = useState(false);

  useEffect(() => {
    if (loginSuccess) {
      const timer = setTimeout(() => {
        router.push("/dashboard");
      }, 1000);
      return () => clearTimeout(timer);
    }
  }, [loginSuccess, router]);

  const form = useForm<LoginFormValues>({
    resolver: zodResolver(loginSchema),
    defaultValues: {
      email: "",
      password: "",
    },
  });

  async function onSubmit(data: LoginFormValues) {
    setServerError(null);
    setIsSubmitting(true);
    setAuthProgress(0);
    setShowPortal(true);

    try {
      await simulateProgress(0, 30);
      await simulateProgress(30, 60);
      await simulateProgress(60, 85);

      const result = await login(data);

      if (result.user) {
        await simulateProgress(85, 100);
        toast({
          title: "Вход выполнен успешно",
          description: "Подготовка к квантовому переходу...",
          duration: 2000,
        });
        setIsSubmitting(false);
        setLoginSuccess(true);
      }
    } catch (error) {
      const errorMessage =
        error instanceof Error ? error.message : "Произошла ошибка при входе";
      setServerError(errorMessage);
      toast({
        variant: "destructive",
        title: "Ошибка входа",
        description: errorMessage,
      });
      setIsSubmitting(false);
      setShowPortal(false);
    }
  }

  const simulateProgress = async (start: number, end: number) => {
    const duration = 800;
    const steps = 10;
    const increment = (end - start) / steps;
    for (let i = 0; i <= steps; i++) {
      setAuthProgress(start + increment * i);
      await new Promise((resolve) => setTimeout(resolve, duration / steps));
    }
  };

  const handlePortalComplete = () => {
    setLoginSuccess(true);
  };

  return (
    <div className="relative">
      <SpaceProgressLoader isLoading={isSubmitting} progress={authProgress} />
      <FlashTransition
        isActive={showPortal}
        onComplete={handlePortalComplete}
        duration={2000}
        color="#3b82f6"
        intensity="low"
      />

      <form onSubmit={form.handleSubmit(onSubmit)} className="space-y-5">
        <div className="relative z-[100]">
          <SpaceInput
            label="Email"
            placeholder="email@example.com"
            type="email"
            {...form.register("email")}
            error={form.formState.errors.email?.message}
          />
        </div>

        <div className="relative z-[100]">
          <SpaceInput
            label="Пароль"
            type="password"
            placeholder="••••••••"
            showPasswordToggle={true}
            {...form.register("password")}
            error={form.formState.errors.password?.message}
          />
        </div>

        {serverError && (
          <div className="bg-red-500/10 border border-red-500/30 rounded-md p-3 text-sm text-red-200">
            {serverError}
          </div>
        )}

        <SpaceButton
          type="submit"
          className="w-full"
          isLoading={isLoading || isSubmitting}
        >
          Войти в систему
        </SpaceButton>
      </form>
    </div>
  );
}
